import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, DropDwnPicker_Cmpt } from "../../../components";
import { OpenModal, CloseModal } from "../../../components/DialogHRM";

import { PickClient_Cmpt } from "..";

import "./dashboard.css"
import { DateCalComponent_Cmpt } from "../../Inventory";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const PickClient = PickClient_Cmpt();

const CheckBox = CheckBox_Cmpt();

const DateCalComponent = DateCalComponent_Cmpt();

const DropDwnPicker = DropDwnPicker_Cmpt();


const frmNm = "upd_client"

const UpdClient  = ({ data, elmId, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const print  = async ()  => {
		let ffm = getStore(frmNm);

		if(ffm && ffm["dateOfBirth"]){
			let nd = (new Date(ffm["dateOfBirth"])).getTime();
			if(nd){
				ffm["dob"] = nd;
			}
		}
	

		let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )

		let bdyq2 = {
			query: "updateClientNotary",
			params:{
				"clientNotaryId": ffm["clientNotaryId"]
			},
			data2update: diff.data
		};

		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		if (td && td.data) {
			updStore(frmNm, {});
			updStore(frmNm + "_bck", {});
			set_obs(gen16CodeId())
			close(true);
			confirm && confirm();
		}
		///let pdfUri = await generateTranslateAddClient(ffm, true, true);
		///window.open(pdfUri);
	};

	let flds = [
		"phoneNumber",
		"firstName","middleName", 
		"lastName","dateOfBirth",
		"genre","placeOfBirth",
		"currentAddress", "father", "mother", 
		"height", "weight", "ss","alienNumber",
		"race","maritalStatus", 
	]

	let diff = compareJSON(flds,getStore(frmNm),getStore(frmNm + "_bck") )


	const close  = ()  => {
		data?.modalID && CloseModal({id:data?.modalID});
	}

	
	const handlePick = (v,f)=>{
		updFrm(f,v.clientNotaryId)
	}



	const openPick = (f)=>{
		let data = {};
		data["zIndex"] = 480;
		// data['observeResize']=true;
		data["props"] = { minHeight: "1vh", overlay: true };
		data["content"] = <PickClient handleClick={(v)=>handlePick(v,f) } />
		OpenModal(data);
	}
	

	return (
		<div style={{
			maxHeight: "80vh",
			width: "960px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title ">
					Actualizar Formulario Cliente
				</p>
				<div className="flexSpace"/>
				
			</div>

			<div className="_dsplFlx " style={{marginTop: 10}}>
				<div className="flexSpace"/>
				<div style={{margin:"30px 10px 0 0"}}>
					<DateCalComponent  
						init={getStore(frmNm)?.["dateOfBirth"]} 
						label={"Fecha de nacimiento"}
						updDate={(e)=>updFrm("dateOfBirth",e)}
					/>
				</div>
				<div className="flexSpace"/>
				
				<div style={{marginTop:39}}>
					<DropDwnPicker 
						init={getStore(frmNm)?.["genre"]}
						lbl_empty={"Sexo"} 
						list={[
							{label: "Mujer", id:"Female"},
							{label: "Hombre", id:"Male"}
							
						]} 
						onSelect={(e)=>updFrm("genre",e)}
					/>
				</div>	
				<div className="flexSpace"/>
			

				<div style={{marginTop:39}}>
					<DropDwnPicker 
						init={getStore(frmNm)?.["maritalStatus"]}
						lbl_empty={"Estado Civil"} 
						list={[
							{label: "Soltero", id:"single"},
							{label: "Casado", id:"married"},
							{label: "Divorciado", id:"divorced"},
							{label: "Viudo", id:"widowed"}
						]} 
						onSelect={(e)=>updFrm("maritalStatus",e)}
						direction={"right"} 
					/>
				</div>	
				<div className="flexSpace"/>
				<div style={{marginTop:39}}>
					<DropDwnPicker 
						init={getStore(frmNm)?.["race"]}
						lbl_empty={"raza"} 
						list={
							[
								{label: "Hispanic or Latino", id:"Hispanic or Latino"},
								{label: "American White", id:"American White"},
								{label: "African", id:"African"},
							]
						}
						onSelect={(e)=>updFrm("race",e)} 
					/>
				</div>	
				<div className="flexSpace"/>
				
				
				
			</div>
			
			<div className="_dsplFlx spaceAround flxWrp " style={{marginTop: 10}}>
			
				
				<TextInput  
					init={getStore(frmNm)?.["firstName"]} 
					label={"Nombre"} 
					width={200} 
					updChanges={(e)=>updFrm("firstName",e)}
				/>
				<TextInput  
					init={getStore(frmNm)?.["middleName"]} 
					label={"Segundo Nombre"} 
					width={200} 
					updChanges={(e)=>updFrm("middleName",e)}
				/>
				<TextInput  
					init={getStore(frmNm)?.["lastName"]} 
					label={"Apellidos"} 
					width={360} 
					updChanges={(e)=>updFrm("lastName",e)}
				/>
				
			</div>


		

			<div className="_dsplFlx " style={{marginTop: 10}}>
				
				<TextInput  
					init={getStore(frmNm)?.["phoneNumber"]} 
					label={"Telefono"} 
					width={120} 
					updChanges={(e)=>updFrm("phoneNumber",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["height"]} 
					label={"Altura"} 
					width={120} 
					updChanges={(e)=>updFrm("height",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["weight"]} 
					label={"Peso"} 
					width={120} 
					updChanges={(e)=>updFrm("weight",e)}
				/>
				<div className="flexSpace"/>
				
				
			</div>

			<div className="_dsplFlx " style={{marginTop: 10}}>
				
				
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["alienNumber"]} 
					label={"Alien Number"} 
					width={120} 
					updChanges={(e)=>updFrm("alienNumber",e)}
				/>
				<div className="flexSpace"/>
				<TextInput  
					init={getStore(frmNm)?.["ss"]} 
					label={"Social"} 
					width={120} 
					updChanges={(e)=>updFrm("ss",e)}
				/>
				
				<div className="flexSpace"/>
			</div>

			
			<div className="separator"/>


			

			<p className="group_title">
				Lugar de Nacimiento 
			</p>
			<div className="_dsplFlx spaceAround "> 
				<TextInput 
					init={getStore(frmNm)?.["placeOfBirth"]?.["city"]} 
					label={"Municipio"} 
					width={200} 
					updChanges={(e)=>updFrm("city",e, "placeOfBirth")}
				/>
				<TextInput  
					init={getStore(frmNm)?.["placeOfBirth"]?.["state"]} 
					label={"Provincia"} 
					width={200} 
					updChanges={(e)=>updFrm("state",e, "placeOfBirth")}
				/>
			</div>

		

			<div className="separator"/>



			<p className="group_title">
				Padres
			</p>
			<div className="_dsplFlx spaceAround flxWrp"> 
				<div>
					<p className="label"  onClick={()=>openPick("father")}>
						{"Padre"}
					</p>
					{getStore(frmNm)?.["father"]?
					<div  
						style={ {width: 240}}
						className="r7gAOb yyJm8b"
					>
						{getStore(frmNm)?.["fatherInfo"]?.["firstName"] && `${getStore(frmNm)?.["fatherInfo"]?.["firstName"]} ${getStore(frmNm)?.["fatherInfo"]?.["lastName"]}`}
					
					</div>
					:null}
				</div>
				<div>
					<p className="label"  onClick={()=>openPick("mother")}>
						{"Madre"}
					</p>
					{getStore(frmNm)?.["mother"]?
					<div  
						style={ {width: 240}}
						className="r7gAOb yyJm8b"
					>
						{getStore(frmNm)?.["motherInfo"]?.["firstName"] && `${getStore(frmNm)?.["motherInfo"]?.["firstName"]} ${getStore(frmNm)?.["motherInfo"]?.["lastName"]}`}
					</div>
					:null}
				</div>
			</div>
			{diff?.hasChange ? (
				<div className="_dsplFlx ">
					<div className="flexSpace"/>
					<div className="card">
						<button onClick={print}>
							Actualizar
						</button>
					</div>
				</div>
			):<div className="card"/>}
		</div>
	);
};

export default UpdClient;


const compareJSON = (fls,obj, bkUp) =>{
	let r = 0;
	let data = {}
	for(let i in fls){
		let k1 = fls[i];
		if(obj[k1] ){
			if(typeof obj[k1] === "object"){
				ObjectKeys(obj[k1]).map(fk2=>{
					if(obj?.[k1]?.[fk2]!==bkUp?.[k1]?.[fk2]){
						r = 1;
						data[k1] = obj[k1];
					}
				})
			}
			else if(obj[k1] &&  obj[k1]!==bkUp[k1]){
				r = 1;
				data[k1] = obj[k1];
			}
		}
	}
	return {data, hasChange:r};
  }